import styles from "./styles.module.css"
import Footer from "../../components/Footer";
import Layout from "../../components/Layout";
import useStateBool from "../../utils/useStateBool";
import StyledWindow from "../../components/StyledWindow";

export default function TimetablePage()
{
	const showTimetable = useStateBool(true);
	const showTimetableFirst = useStateBool(true);
	const showTurnamentFirst = useStateBool(true);

	return (
		<Layout centeredPage headerColor="#4d0709" className={styles.root} footer={<Footer curPage="timetable" />}>
			<div className={styles.background}></div>
			<h1 className={styles.title}>Underparty</h1>
			<div className={styles.body}>
				{showTimetable.v ?
					<StyledWindow
						className={styles.list}
						footer={
							<div className={styles.btns}>
								<button onClick={showTimetableFirst.setT} className={showTimetableFirst.v ? styles.active : ""}></button>
								<button onClick={showTimetableFirst.setF} className={!showTimetableFirst.v ? styles.active : ""}></button>
							</div>
						}
					>
						<div className={styles.items}>
							{(showTimetableFirst.v ? data_timetable_first : data_timetable_second)
								.map((el, i) => <div key={i}>{el}</div>)}
						</div>
					</StyledWindow>
					:
					<StyledWindow
						className={styles.list}
						footer={
							<div className={styles.btns}>
								<button onClick={showTurnamentFirst.setT} className={showTurnamentFirst.v ? styles.active : ""}>Under</button>
								<button onClick={showTurnamentFirst.setF} className={!showTurnamentFirst.v ? styles.active : ""}></button>
							</div>
						}
					>
						<div className={styles.items}>
							{(showTurnamentFirst.v ? data_turnament_first : data_turnament_second)
								.map((el, i) => <div key={i}>{el}</div>)}
						</div>
					</StyledWindow>
				}
				<div className={styles.btns}>
					<button onClick={showTimetable.setT} className={showTimetable.v ? styles.active : ""}></button>
					<button onClick={showTimetable.setF} className={!showTimetable.v ? styles.active : ""}></button>
				</div>
			</div>
		</Layout>
	);
}

